.filterDropdown > button{
  padding: 0;
  text-align: left;
  background: none !important;
  border-color: #ccc !important;
  box-shadow: none !important;
}
.filterDropdown > button:after{
  display: none;
}
.report-request{
  width: 310px;
  margin: auto;
}
.report-request h4{
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  position: relative;
  display: block;
  margin-top: 50px;
}
.report-request h4:after{
  position: absolute;
  content: attr(x);
  width: 100%;
  height: 2px;
  background: #ccc;
  left: 0;
  top: 8px;
}
.report-request h4 span{
  background: #fff;
  position: relative;
  z-index: 1;
  padding: 0 10px;
}
.report-request>* {
  display: inline-block;
}

.report-request .dropdown {
  padding: 0;
  position: relative;
}

.report-request .dropdown .dropdown-menu {
  width: 100%;
  padding: 0;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.21);
}

.report-request .dropdown .dropdown-menu li {
  font-size: 14px;
  color: #656565;
  padding: 8px 15px;
  cursor: pointer;
  transition: all linear 0.3s 0s;
}

.report-request .dropdown .dropdown-menu li:hover {
  background: #fcfcfc;
}

.report-request .dropdown .dropdown-menu li.active {
  background: #fcfcfc;
  color: #31bae6;
}

.report-request .dropdown .filterDropdown {
  padding: 0;
  text-align: left;
  box-shadow: none !important;
  background-color: #fff !important;
}

.report-request .date {
  width: 310px;
  border-radius: 3px;
  background-color: #ffffff;
  padding: 6px 15px;
  background: url("data:image/png;base64,") no-repeat right;
  background-position: 98%;
}

.report-request .date .startEndDate {
  font-size: 13px;
  font-weight: 500;
  color: #4c5054;
  margin: 0;
  margin-right: 25px;
  line-height: 23px;
}

.report-request .date .startEndDate .getDate {
  font-weight: normal;
  font-size: 13px;
  color: #4c5054;
  position: relative;
  top: 0;
  left: 2px;
}

.report-request .listFilters {
  list-style: none;
  padding: 10px 0 10px 0;
  margin: 0;
}

.report-request .filterButton {
  width: 100%;
  margin-top: 27px;
  height: 30px;
  background: #03bbd4;
  border: none;
  border-radius: 3px;
  color: #fff;
  font-size: 13px;
  font-weight: 500;
}

.report-request .dateCon {
  padding: 0 10px 0;
  overflow: auto;
}

.report-request .dateCon>div {
  padding: 0 5px;
  width: calc(50% - 26px);
  float: left;
}

.report-request .dateCon>div label {
  margin-bottom: 2px;
  display: block;
  font-size: 12px;
  color: #9e9e9e;
  margin-top: 8px;
}

.report-request .dateCon>div input[type="text"] {
  height: 30px !important;
  padding: 0 10px !important;
  border-radius: 3px;
  border: solid 1px #e7e7e7;
  width: 100%;
  background: url("data:image/png;base64,") no-repeat right;
  background-position: 95%;
  font-size: 12px;
  box-shadow: none;
  outline: none;
}

.report-request .dateCon>div:last-child {
  width: 52px;
}

.report-request .react-datepicker__day--selected, .report-request .react-datepicker__day--in-selecting-range, .report-request .react-datepicker__day--in-range{
  background-color: #03bbd4;
}



